.arts {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 2px;
    justify-content: center;

    .art {
        list-style-type: none;
        padding: 0;

        .art-div {
            display: flex;
            flex-direction: column;
            align-items: center;
            border-style: solid;
            width: fit-content;
            height: fit-content;
            border-color: #2EC4B6;
            border-width: 1px;

            .art-image {
                width: 10rem;
                height: 10rem;
                object-fit: cover;
                object-position: top;
            }
            // .art-date {
            //     text-align: center;
            //     width: 100%;
            //     margin: 0;
            //     color: #011627;
            //     background-color: #2EC4B6;
            // }
        }
    }
}